<template>
  <ul class="article-list">
    <li
      v-for="item in articleList"
      :key="item.title"
    >
      <div class="article-title">{{ item.title }}</div>
      <div class="article-abstract">{{ item.abstract }}</div>
      <div class="article-date">{{ item.date }}</div>
    </li>
  </ul>
</template>

<script>
export default {
  data () {
    return {
      articleList: [
        {
          "title": "",
          "abstract": "",
          "date": "2019-01-02 20:55"
        }, {
          "title": "",
          "abstract": "asdfasdfwqsdlkfjaslkdjflkasjdflmzxc.v,m",
          "date": "2019-01-02 20:55"
        }, {
          "title": "",
          "abstract": "VV",
          "date": "2019-01-02 20:55"
        }, {
          "title": "",
          "abstract": "",
          "date": "2019-01-02 20:55"
        },
      ]
    }
  }
}
</script>

<style scoped>
ul li {
  /*  */
  list-style-type: none;
}
.article-title {
  font-size: 30px;
  font-weight: 1000;
  margin-bottom: 25px;
}
.article-abstract {
  font-size: 20px;
  margin-top: 3px;
  margin-bottom: 8px;
}
.article-date {
  font-size: 20px;
  margin-top: 3px;
  margin-bottom: 16px;
}
</style>



